import { siteData, siteDataFr } from '@/data/siteData';
import { Card } from '@/components/ui/Card';
import { Button } from '@/components/ui/Button';
import { generateWhatsAppLink, createBookingMessage } from '@/lib/whatsapp';
import { Badge } from '@/components/ui/Badge';

// Helper to safely access pricing properties
// eslint-disable-next-line @typescript-eslint/no-explicit-any
const formatPriceTier = (tier: any) => {
    let priceDisplay = '';
    if ('totalPrice' in tier) {
        priceDisplay = `${tier.totalPrice}€ Total`;
    } else if ('pricePerPerson' in tier) {
        priceDisplay = `${tier.pricePerPerson}€ / pers`;
    }

    let peopleDisplay = '';
    if (tier.minPeople && tier.maxPeople) {
        peopleDisplay = `${tier.minPeople}-${tier.maxPeople} people`;
    } else if (tier.minPeople) {
        peopleDisplay = `${tier.minPeople}+ people`;
    } else if (tier.maxPeople) {
        peopleDisplay = `Up to ${tier.maxPeople} people`;
    } else {
        peopleDisplay = 'Fixed Price';
    }

    return { priceDisplay, peopleDisplay };
};

export default async function ServicesPage({
    params,
}: {
    params: Promise<{ locale: string }>;
}) {
    const { locale } = await params;
    const data = locale === 'fr' ? siteDataFr : siteData;
    const t = await import(`../../../../messages/${locale}.json`);

    return (
        <div className="min-h-screen bg-secondary pt-24 pb-16">
            <div className="container mx-auto px-4">
                <h1 className="text-4xl font-bold mb-4 font-poppins text-neutral-dark text-center">
                    {t.Header.services}
                </h1>
                <p className="text-center text-gray-600 mb-16 max-w-2xl mx-auto">
                    Explore our curated selection of tours, activities, packages, and transport options designed for your comfort.
                </p>

                {/* Tours Section */}
                <section className="mb-20" id="tours">
                    <h2 className="text-3xl font-bold mb-8 font-poppins text-primary border-b border-primary/20 pb-2">Tours</h2>
                    <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                        {data.tours.map((tour) => (
                            <Card key={tour.id} className="flex flex-col h-full">
                                <div className="mb-4">
                                    <Badge variant="accent" className="mb-2">
                                        {tour.duration === '1_day' ? 'Full Day' : 'Half Day'}
                                    </Badge>
                                    <h3 className="text-xl font-bold font-poppins text-neutral-dark">{tour.name}</h3>
                                </div>

                                <div className="flex-grow space-y-4 mb-6">
                                    {/* Pricing Display */}
                                    <div className="bg-neutral-light p-4 rounded-lg text-sm">
                                        {tour.pricing.map((tier, idx) => {
                                            const { priceDisplay, peopleDisplay } = formatPriceTier(tier);
                                            return (
                                                <div key={idx} className="flex justify-between items-center mb-1 last:mb-0">
                                                    <span className="text-gray-600">
                                                        {peopleDisplay}
                                                    </span>
                                                    <span className="font-bold text-primary">
                                                        {priceDisplay}
                                                    </span>
                                                </div>
                                            );
                                        })}
                                    </div>
                                </div>

                                <div className="mt-auto">
                                    <Button
                                        fullWidth
                                        href={generateWhatsAppLink(createBookingMessage(tour.name, 'tour'))}
                                        target="_blank"
                                    >
                                        Book on WhatsApp
                                    </Button>
                                </div>
                            </Card>
                        ))}
                    </div>
                </section>

                {/* Activities Section (Moved from separate page) */}
                <section className="mb-20" id="activities">
                    <h2 className="text-3xl font-bold mb-8 font-poppins text-primary border-b border-primary/20 pb-2">Activities</h2>
                    <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
                        {/* Activity Cards */}
                        <div className="lg:col-span-2 grid grid-cols-1 md:grid-cols-2 gap-6">
                            {data.activities.experiences.map((activity) => (
                                <Card key={activity.id} className="flex flex-col relative overflow-hidden group">
                                    <div className="absolute top-0 right-0 bg-primary text-white px-3 py-1 text-sm font-bold rounded-bl-lg">
                                        {activity.price}€
                                    </div>
                                    <div className="mb-4 pt-4">
                                        <h3 className="text-xl font-bold font-poppins text-neutral-dark capitalize">
                                            {activity.type} <span className="text-gray-400 font-normal text-base">at {activity.location}</span>
                                        </h3>
                                    </div>
                                    <p className="text-gray-600 mb-6 flex-grow">
                                        {activity.type === 'quad'
                                            ? "Adrenaline-filled ride through dramatic landscapes."
                                            : "Traditional and relaxing camel trek."}
                                    </p>
                                    <Button
                                        href={generateWhatsAppLink(createBookingMessage(`${activity.type} at ${activity.location}`, 'activity'))}
                                        fullWidth
                                        target="_blank"
                                    >
                                        Book Activity
                                    </Button>
                                </Card>
                            ))}
                        </div>

                        {/* Activity Sidebar Info */}
                        <div className="lg:col-span-1 space-y-6">
                            <Card className="bg-white/80 backdrop-blur-sm">
                                <h3 className="text-lg font-bold font-poppins mb-4 text-neutral-dark">Available Times</h3>
                                <div className="space-y-4">
                                    {data.activities.timeSlots.map((slot) => (
                                        <div key={slot.id} className="flex justify-between items-center border-b border-gray-100 last:border-0 pb-2 last:pb-0">
                                            <span className="font-medium capitalize text-gray-700">{slot.id}</span>
                                            <span className="text-primary font-bold">{slot.from} - {slot.to}</span>
                                        </div>
                                    ))}
                                </div>
                            </Card>

                            <Card className="bg-neutral-dark text-white">
                                <h3 className="text-lg font-bold font-poppins mb-4 text-white">Enhance Your Trip</h3>
                                {data.activities.addons.map((addon) => (
                                    <div key={addon.id}>
                                        <div className="flex justify-between items-center mb-2">
                                            <span className="font-bold text-lg">{addon.name}</span>
                                            <span className="text-xl text-primary">{addon.pricePerPerson}€</span>
                                        </div>
                                        <p className="text-gray-400 text-sm mb-4">
                                            Available from {addon.availableFrom} to {addon.availableTo}
                                        </p>
                                        <Button
                                            variant="primary"
                                            fullWidth
                                            href={generateWhatsAppLink(createBookingMessage(`Addon: ${addon.name}`, 'activity'))}
                                            target="_blank"
                                        >
                                            Add {addon.name}
                                        </Button>
                                    </div>
                                ))}
                            </Card>
                        </div>
                    </div>
                </section>

                {/* Packages Section */}
                <section className="mb-20" id="packages">
                    <h2 className="text-3xl font-bold mb-8 font-poppins text-primary border-b border-primary/20 pb-2">Packages</h2>
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                        {data.packages.map((pkg) => (
                            <Card key={pkg.id} className="bg-white">
                                <div className="flex flex-col md:flex-row gap-6">
                                    <div className="flex-grow">
                                        <h3 className="text-xl font-bold font-poppins text-neutral-dark mb-2">{pkg.name}</h3>
                                        <p className="text-gray-500 mb-4">{pkg.location}</p>
                                        <div className="flex flex-wrap gap-2 mb-4">
                                            {pkg.included.map((item) => (
                                                <Badge key={item} variant="neutral">{item}</Badge>
                                            ))}
                                        </div>
                                    </div>
                                    <div className="flex flex-col justify-center items-end min-w-[120px]">
                                        <span className="text-3xl font-bold text-primary mb-2">{pkg.price}€</span>
                                        <Button
                                            variant="primary"
                                            size="sm"
                                            href={generateWhatsAppLink(createBookingMessage(pkg.name, 'package'))}
                                            target="_blank"
                                        >
                                            Book Offer
                                        </Button>
                                    </div>
                                </div>
                            </Card>
                        ))}
                    </div>
                </section>

                {/* Transport Section */}
                <section id="transport">
                    <h2 className="text-3xl font-bold mb-8 font-poppins text-primary border-b border-primary/20 pb-2">Transport</h2>
                    <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                        {data.transport.map((trans) => (
                            <Card key={trans.id} className="text-center hover:bg-neutral-light transition-colors">
                                <h3 className="text-lg font-bold font-poppins mb-4">{trans.name}</h3>
                                <Button
                                    variant="outline"
                                    size="sm"
                                    href={generateWhatsAppLink(createBookingMessage(trans.name, 'transport'))}
                                    target="_blank"
                                >
                                    Inquire Rate
                                </Button>
                            </Card>
                        ))}
                    </div>
                </section>
            </div>
        </div>
    );
}
